/* eslint-disable implicit-arrow-linebreak */
import logo from 'old/images/logo-full.png'
import React from 'react'
import { matchPath, useLocation } from 'react-router-dom'
import {
  CHECKOUT_RESULT,
  CHECKOUT_STEP_1,
  CHECKOUT_STEP_2,
  CHECKOUT_STEP_3,
  CHECKOUT_STEP_3A,
  CHECKOUT_STEP_4,
  FEATURED_PRODUCTS,
  HOME,
  RECOMMENDED_PRODUCTS,
  SHOP,
  VIEW_PRODUCT
} from 'old/constants/routes'
import styled from 'styled-components'

const Footer = () => {
  const { pathname } = useLocation()

  const visibleOnlyPath = [
    HOME,
    SHOP,
    FEATURED_PRODUCTS,
    RECOMMENDED_PRODUCTS,
    VIEW_PRODUCT,
    CHECKOUT_STEP_1,
    CHECKOUT_STEP_2,
    CHECKOUT_STEP_3,
    CHECKOUT_STEP_3A,
    CHECKOUT_STEP_4,
    CHECKOUT_RESULT
  ]

  const matchRoute = matchPath(pathname, {
    path: visibleOnlyPath,
    exact: true,
    strict: false
  })

  if (!matchRoute) return null

  return (
    <Container>
      <div className="footer-col-1" />
      <div className="footer-col-2">
        <Logo alt="Footer logo" src={logo} />

        <h5>
          &copy;&nbsp;
          {new Date().getFullYear()}
        </h5>
      </div>
      <div className="footer-col-3" />
    </Container>
  )
}

const Container = styled.footer`
  position: relative;
  padding: ${(props) => `0 ${props.theme.space['xxl-size']}`};
  margin-top: ${(props) => props.theme.space['xl-size']};
  background: ${(props) => props.theme.colors['off-white']};
  display: flex;
  align-items: center;
  justify-content: space-between;

  /* @include mobile {
    padding: 0;
    padding-top: 2.5rem;
    flex-direction: column;
    text-align: center;
  } */

  a {
    text-decoration: underline;
  }
`

const Logo = styled.img`
  width: 15rem;
  height: 6rem;
  object-fit: contain;
`

export default Footer
