/* eslint-disable react/jsx-one-expression-per-line */
import React, { useContext } from 'react'
import { Flexbox, Box } from 'components'
import { useTranslation } from 'react-i18next'

import { basketContext } from 'providers/BasketProvider'
import { basketToggleContext } from 'providers/BasketToggleProvider'

const Header = () => {
  const { t } = useTranslation('common')
  const { closeBasket } = useContext(basketToggleContext)
  const { clearBasket, basketLength } = useContext(basketContext)

  let countName = 'basket.products'

  if (basketLength > 4) {
    countName = 'basket.products_multiple'
  } else if (basketLength > 1) {
    countName = 'basket.products_plural'
  }

  return (
    <Flexbox
      padding="m-size"
      width="100%"
      backgroundColor="white"
      // @ts-ignore
      zIndex="basket"
      alignItems="center"
    >
      <Box as="h3" flexGrow={1}>
        {t('basket.title')}

        {!!basketLength && <span>{t(countName, { count: basketLength })}</span>}
      </Box>

      {!!basketLength && (
        <Box marginRight="1rem">
          <button
            className="basket-clear button button-border button-border-gray button-small"
            disabled={basketLength === 0}
            onClick={clearBasket}
            type="button"
          >
            <span>{t('basket.clear')}</span>
          </button>
        </Box>
      )}

      <span
        className="basket-toggle button button-border button-border-gray button-small"
        onClick={closeBasket}
        role="presentation"
      >
        {t('basket.close')}
      </span>
    </Flexbox>
  )
}

export default Header
