import { ImageLoader } from 'old/components/common'
import React from 'react'
import { generatePath, useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { displayMoney } from 'old/helpers/utils'
import { Product } from 'models/product'
import { VIEW_PRODUCT } from 'constants/routes'

type Props = {
  product: Product
}

const ProductTile = ({ product }: Props) => {
  const history = useHistory()
  const viewProductPath = generatePath(VIEW_PRODUCT, {
    id: product.id
  })

  return (
    <Container
      onClick={() => history.push(viewProductPath)}
      role="presentation"
    >
      <ImageWrapper>
        <ImageLoader
          alt=""
          className="product-card-img"
          src={product.gallery[0].url}
        />
      </ImageWrapper>

      <InfoWrapper>
        <h3>{product.name}</h3>
        <p className="text-subtle text-italic">{displayMoney(product.price)}</p>
      </InfoWrapper>
    </Container>
  )
}

const Container = styled.div`
  width: 100%;
  max-height: 30rem;
  border: 1px solid ${(props) => props.theme.colors['border-color']};

  &:hover {
    cursor: pointer;

    & > * img {
      transform: scale(1.1);
    }
  }
`

const ImageWrapper = styled.div`
  width: 100%;
  height: 20rem;
  background: ${(props) => props.theme.colors.white};
  position: relative;
  overflow: hidden;
  padding: 1rem;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    /* @include bezier-transition; */
  }
`

const InfoWrapper = styled.div`
  padding: 1.5rem;

  h3 {
    margin: 0;
  }

  p {
    margin-top: 0;
    margin-bottom: 0;
  }
`

export default ProductTile
