import React, { useState } from 'react'
import { useParams } from 'react-router-dom'
import styled from 'styled-components'
import { useQuery } from 'react-query'
import { QueryLoader, Page, Separator, Flexbox } from 'components'
import { getProduct } from 'api'
import { useDocumentTitle, useScrollTop } from 'hooks'

import Form from './Form'
import BackButton from './BackButton'
import Images from './Images'

const ViewProduct = () => {
  const { id } = useParams<{ id: string }>()

  useScrollTop()
  useDocumentTitle()

  const productQuery = useQuery(`product/${id}`, () => getProduct({ id }))

  const [variant, setVariant] = useState<string>()

  const onSelect = (value?: string) => {
    setVariant(value)
  }

  return (
    <Page>
      <QueryLoader query={productQuery}>
        {(data) => (
          <>
            <BackButton />

            <ProductModal>
              <Images variant={variant} product={data} />

              <Flexbox flexGrow={1} flexDirection="column" marginLeft="3rem">
                <h1 className="margin-top-0">{data.name}</h1>
                <span style={{ width: '100%' }}>{data.description}</span>
                <br />
                <br />
                <Separator />

                <Form onSelect={onSelect} product={data} />
              </Flexbox>
            </ProductModal>

            {/* <Display
                  marginTop="10rem"
                  title={t('productPage.recommended.title')}
                  error={errorFeatured}
                  isLoading={isLoadingFeatured}
                  fetchProducts={fetchRecommendedProducts}
                  products={recommendedProducts}
                /> */}
          </>
        )}
      </QueryLoader>
    </Page>
  )
}

const ProductModal = styled.div`
  display: flex;

  width: 100%;
  padding: 30px;
  background: ${(props) => props.theme.colors.white};
  border: 1px solid ${(props) => props.theme.colors['border-color']};
`

export default ViewProduct
