import { Timestamp } from 'old/types'

export const displayDate = (timestamp: Timestamp) => {
  const date = new Date(timestamp)
  const monthNames = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December'
  ]

  const day = date.getDate()
  const monthIndex = date.getMonth()
  const year = date.getFullYear()

  return `${monthNames[monthIndex]} ${day}, ${year}`
}

export const displayMoney = (n: number) => {
  const format = new Intl.NumberFormat('pl-PL', {
    style: 'currency',
    currency: 'PLN'
  })

  // or use toLocaleString()
  return format.format(n)
}

export const displayActionMessage = (
  msg: string,
  status: 'info' | 'success' | 'error' = 'info'
) => {
  const div = document.createElement('div')
  const span = document.createElement('span')

  div.className = `toast toast-${status}`
  span.className = 'toast-msg'
  span.textContent = msg
  div.appendChild(span)

  if (document.querySelector('.toast')) {
    // @ts-ignore
    document.body.removeChild(document.querySelector('.toast'))
    document.body.appendChild(div)
  } else {
    document.body.appendChild(div)
  }

  setTimeout(() => {
    try {
      document.body.removeChild(div)
    } catch (e) {
      console.log(e)
    }
  }, 3000)
}
