import React, { createContext, ReactNode, useState } from 'react'

type AuthStatus = {
  isSuccess: boolean
  isAuthenticating: boolean
  isError: boolean
  message?: string
}

type AuthStatusContexType = {
  authStatus: AuthStatus
  setAuthStatus: React.Dispatch<React.SetStateAction<AuthStatus>>
}

export const authStatusContext = createContext<AuthStatusContexType>({
  authStatus: {
    isSuccess: false,
    isAuthenticating: false,
    isError: false
  },
  setAuthStatus: () => undefined
})

type Props = {
  children: ReactNode
}
const AuthStatusProvider = ({ children }: Props) => {
  const [authStatus, setAuthStatus] = useState<
    AuthStatusContexType['authStatus']
  >({
    isSuccess: false,
    isAuthenticating: false,
    isError: false
  })

  return (
    <authStatusContext.Provider value={{ authStatus, setAuthStatus }}>
      {children}
    </authStatusContext.Provider>
  )
}

export default AuthStatusProvider
