import { Flexbox, Separator, Square } from 'components'

import styled from 'styled-components'
import React, { useEffect, useState } from 'react'
import { ImageLoader } from 'old/components/common'

import { Product } from 'models/product'

type Props = {
  product: Product
  variant?: string
}

const Images = ({ product, variant }: Props) => {
  const { name, gallery } = product
  const [selectedImage, setSelectedImage] = useState<string>(gallery[0].url)

  useEffect(() => {
    const variantImage = gallery.find((img) => img.variant === variant)?.url

    if (variantImage) {
      setSelectedImage(variantImage)
    }
  }, [variant])

  return (
    <Flexbox
      flexGrow={4}
      flexDirection="column"
      minWidth="40%"
      maxWidth="500px"
    >
      <Square>
        <ImageWrapper>
          <ImageLoader
            alt={name}
            className="product-modal-image"
            src={selectedImage}
          />
        </ImageWrapper>
      </Square>

      <Separator />

      {gallery.length !== 0 && (
        <Gallery>
          {gallery.map((image) => (
            <GalleryImageWrapper
              key={image.id}
              onClick={() => setSelectedImage(image.url)}
              role="presentation"
            >
              <GalleryImage src={image.url} />
            </GalleryImageWrapper>
          ))}
        </Gallery>
      )}
    </Flexbox>
  )
}

const ImageWrapper = styled.div`
  width: 100%;
  height: 100%;
  flex-grow: 1;
  position: relative;

  input[type='color'] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    mix-blend-mode: hue;
  }
`

const Gallery = styled.div`
  width: 100%;
  background: #fff;
  padding: 5px;
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, 60px);
  grid-template-rows: repeat(auto-fit, 60px);
  grid-gap: 5px;

  /* &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #1a1a1a;
    outline: 1px solid #f8f8f8;
    // border-radius: .6rem;
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.2);
  } */
`

const GalleryImage = styled(ImageLoader)`
  width: 100%;
  height: 100%;
  object-fit: contain;
`

const GalleryImageWrapper = styled.div`
  min-width: 100%;
  max-width: 100%;
  min-height: 60px;
  max-height: 60px;
  border: 1px solid #e1e1e1;
  margin-right: 5px;
  position: relative;

  &:hover {
    cursor: pointer;
    background: #fff;
  }

  &:last-child {
    margin-right: 0;
  }
`

export default Images
