import { doc, getDoc, getFirestore } from 'firebase/firestore'
import { object, string, enum as zenum, number, TypeOf } from 'zod'
import parseApiResponse from './parseApiResponse'

type GetProfilePayload = {
  id: string
}

const profile = object({
  email: string(),
  role: zenum(['USER', 'ADMIN']),
  avatar: string(),
  banner: string(),
  dateJoined: number()
})

export type Profile = TypeOf<typeof profile>

const getProfile = async (payload: GetProfilePayload) => {
  const firestore = getFirestore()
  const ref = doc(firestore, 'users', payload.id)

  const response = await getDoc(ref)
  const parsedResponse = parseApiResponse(profile, response)

  return parsedResponse
}

export default getProfile
