import React, { useEffect, useState } from 'react'
import styled, { css } from 'styled-components'
import BasketToggle from './BasketToggle'
import Logo from './Logo'
import MainMenu from './MainMenu'

const Navigation = () => {
  const [isExpanded, setIsExpanded] = useState(false)

  useEffect(() => {
    const scrollHandler = () => {
      if (window.pageYOffset >= 70) {
        setIsExpanded(true)
      } else {
        setIsExpanded(false)
      }
    }

    window.addEventListener('scroll', scrollHandler)

    return () => window.removeEventListener('scroll', scrollHandler)
  }, [])

  return (
    <Container isExpanded={isExpanded}>
      <Logo />

      <MainMenu />

      <NavigationMenu>
        <NavigationMenuItem>
          <BasketToggle />
        </NavigationMenuItem>
      </NavigationMenu>
    </Container>
  )
}

type ContainerProps = {
  isExpanded: boolean
}

const Container = styled.nav<ContainerProps>`
  width: 100%;
  height: 10rem;
  background: ${(props) => props.theme.colors['nav-bg']};
  display: flex;
  align-items: center;
  padding: 0.5rem 5rem;
  padding-top: 3rem;
  position: absolute;
  top: 0;
  transform: translateY(0);
  z-index: ${(props) => props.theme.zIndices.navigation};
  box-shadow: none;
  /* @include bezier-transition(transform, 0.3s, ease); */

  ${(props) => {
    if (props.isExpanded) {
      return css`
        position: fixed;
        /* animation: slide-down 0.3s ease 1; */
        /* animation-fill-mode: forwards; */
        top: 0;
        height: 6rem;
        padding-top: 0.5rem;
        background ${props.theme.colors['nav-bg-scrolled']};
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.02);
      `
    }

    return undefined
  }}
`

const NavigationMenu = styled.ul`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0;
  margin: 0;
  text-align: right;
`

export const NavigationMenuItem = styled.li`
  display: inline-block;
  list-style-type: none;
  padding: 0;
  margin: 0;
`

export default Navigation
