import { QueryLoader } from 'components'
import { Product } from 'models/product'
import React from 'react'
import { UseQueryResult } from 'react-query'
import ProductsGrid from './Grid'

type Props = {
  query: UseQueryResult<Product[]>
}

const ProductsList = ({ query }: Props) => (
  <QueryLoader query={query}>
    {(data) => <ProductsGrid products={data} />}
  </QueryLoader>
)

export default ProductsList
