import React, { ReactNode } from 'react'
import { GridAreaProps } from 'styled-system'
import { Box } from 'components'

type Props = {
  disabled?: boolean
  onClick: () => void
  children: ReactNode
} & GridAreaProps

const SimpleButton = ({ disabled, onClick, children, ...props }: Props) => (
  <Box width="3rem" height="3rem" {...props}>
    <button
      className="button button-border button-border-gray button-small basket-control basket-control-add"
      disabled={disabled}
      onClick={onClick}
      type="button"
    >
      {children}
    </button>
  </Box>
)

export default SimpleButton
