import { LoadingOutlined } from '@ant-design/icons'
import PropType from 'prop-types'
import React, { useState } from 'react'
import styled from 'styled-components'

type Props = {
  src: string
  alt?: string
  className?: string
}

const ImageLoader = ({ src, alt = '', className = 'image-loader' }: Props) => {
  const loadedImages = {}

  // @ts-ignore
  const [loaded, setLoaded] = useState(loadedImages[src])

  const onLoad = () => {
    // @ts-ignore
    loadedImages[src] = true
    setLoaded(true)
  }

  return (
    <>
      {!loaded && (
        <LoadingOutlined
          style={{
            position: 'absolute',
            top: 0,
            bottom: 0,
            right: 0,
            left: 0,
            margin: 'auto'
          }}
        />
      )}
      <Image
        alt={alt || ''}
        className={`${className || ''} ${
          loaded ? 'is-img-loaded' : 'is-img-loading'
        }`}
        onLoad={onLoad}
        src={src}
      />
    </>
  )
}

const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: contain;
`

export default ImageLoader
