import React, { ReactNode } from 'react'

type Props = {
  count: number
  children?: ReactNode
  stanalone?: boolean
}

const Badge = ({ count, children, stanalone }: Props) => (
  <div className="badge">
    {children}

    {count >= 1 && (
      <span
        className={`badge-count${!stanalone ? ' badge-count-position' : ''}`}
      >
        {count}
      </span>
    )}
  </div>
)

export default Badge
