/* eslint-disable react/no-array-index-key */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable max-len */
import React, { Fragment, useContext } from 'react'
import { Flexbox, Separator } from 'components'

import { basketContext } from 'providers/BasketProvider'
import styled from 'styled-components'
import { basketToggleContext } from 'providers/BasketToggleProvider'
import { useTranslation } from 'react-i18next'
import Header from './Header'
import Checkout from './Checkout'
import BasketItem from './BasketItem'

const Basket = () => {
  const { t } = useTranslation('common')

  const { isOpen } = useContext(basketToggleContext)
  const { basket, basketLength } = useContext(basketContext)

  return (
    <Container isOpen={isOpen}>
      <Header />

      <Flexbox
        flexGrow={1}
        flexDirection="column"
        overflowY="auto"
        padding="s-size"
      >
        {!basketLength && (
          <Flexbox alignItems="center" justifyContent="center" flexGrow={1}>
            <h5 className="basket-empty-msg">{t('basket.empty')}</h5>
          </Flexbox>
        )}

        {basket.map((product, index) => (
          <Fragment
            key={`${product.id}${product.variant ? `_${product.variant}` : ''}`}
          >
            <BasketItem product={product} />
            {basketLength - 1 !== index && <Separator />}
          </Fragment>
        ))}
      </Flexbox>

      <Checkout />
    </Container>
  )
}

type ContainerProps = {
  isOpen: boolean
}

const Container = styled.div<ContainerProps>`
  width: 60rem;
  height: 100vh;
  background: ${(props) => props.theme.colors.white};
  position: fixed;
  top: 0;
  right: 0;
  /* transform: translateX(100%); */
  display: ${(props) => (props.isOpen ? 'flex' : 'none')};
  flex-direction: column;

  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.08);
  z-index: ${(props) => props.theme.zIndices.basket};
`

export default Basket
