import { useDidMount } from 'old/hooks'
import { useEffect, useState } from 'react'
import firebase from 'old/services/firebase'
import { ProductBase } from 'old/types'

const useFeaturedProducts = (itemsCount: number) => {
  const [featuredProducts, setFeaturedProducts] = useState<ProductBase[]>([])
  const [isLoading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const didMount = useDidMount(true)

  const fetchFeaturedProducts = async () => {
    try {
      setLoading(true)
      setError('')

      const docs = await firebase.getFeaturedProducts(itemsCount)

      if (docs.empty) {
        if (didMount) {
          setError('No featured products found.')
          setLoading(false)
        }
      } else {
        const items: ProductBase[] = []

        docs.forEach((snap) => {
          const data = snap.data() as Omit<ProductBase, 'id'>
          items.push({ id: snap.ref.id, ...data })
        })

        if (didMount) {
          setFeaturedProducts(items)
          setLoading(false)
        }
      }
    } catch (e) {
      if (didMount) {
        setError('Failed to fetch featured products')
        setLoading(false)
      }
    }
  }

  useEffect(() => {
    if (featuredProducts.length === 0 && didMount) {
      fetchFeaturedProducts()
    }
  }, [])

  return {
    featuredProducts,
    fetchFeaturedProducts,
    isLoading,
    error
  }
}

export default useFeaturedProducts
