import { BasketItem } from 'models/basketItem'
import { Product } from 'models/product'
import { clearBasket } from 'old/redux/actions/basketActions'
import React, { createContext, ReactNode, useState } from 'react'

type BasketContex = {
  basket: BasketItem[]
  setBasket: React.Dispatch<React.SetStateAction<BasketItem[]>>
  clearBasket: () => void
  basketLength: number
}

export const basketContext = createContext<BasketContex>({
  basket: [],
  setBasket: () => undefined,
  clearBasket: () => undefined,
  basketLength: 0
})

type Props = {
  children: ReactNode
}
const BasketProvider = ({ children }: Props) => {
  const [basket, setBasket] = useState<BasketContex['basket']>([])

  const clearBasket = () => setBasket([])

  const basketLength = basket.length

  console.log(basket)
  return (
    <basketContext.Provider
      value={{ basket, setBasket, clearBasket, basketLength }}
    >
      {children}
    </basketContext.Provider>
  )
}

export default BasketProvider
