import { number, string, object, array, boolean, TypeOf } from 'zod'

const galleryItem = object({
  id: string(),
  url: string(),
  variant: string().optional()
})

const variant = object({
  value: string(),
  label: string()
})

const product = object({
  id: string(),
  dateAdded: number(),
  description: string(),
  gallery: array(galleryItem),
  isFeatured: boolean().optional(),
  isRecommended: boolean().optional(),
  name: string(),
  price: number(),
  variantsLabel: string().optional(),
  variants: array(variant).optional()
})

export type Product = TypeOf<typeof product>

export default product
