/* eslint-disable react/forbid-prop-types */
import React, { useContext, useEffect } from 'react'
import AppRouter from 'pages/App/AppRouter'
import { hot } from 'react-hot-loader/root'
import { useMutation } from 'react-query'
import { getAuth } from 'firebase/auth'
import { authContext } from 'old/providers/authProvider'
import { profileContext } from 'old/providers/profileProvider'
import getProfile from 'old/api/getProfile'
import { Preloader } from 'commonComponents'

const App = () => {
  const { auth, setAuth } = useContext(authContext)
  const { setProfile } = useContext(profileContext)
  const { mutateAsync: mutateGetProfile } = useMutation(getProfile)

  useEffect(() => {
    const auth2 = getAuth()
    auth2.onAuthStateChanged(async (user) => {
      if (user) {
        setAuth(user)

        const alfa = await mutateGetProfile({ id: user.uid })
        setProfile(alfa)
      } else {
        setAuth(null)
      }
    })
  }, [])

  if (auth === undefined) {
    return <Preloader />
  }

  return <AppRouter />
}

export default hot(App)
