/* eslint-disable no-extra-semi */
/* eslint-disable implicit-arrow-linebreak */
import { useDidMount } from 'old/hooks'
import { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import firebase from 'old/services/firebase'
import { ProductBase } from 'old/types'

const useProduct = (id: string) => {
  const storeProduct = useSelector<any, ProductBase>((state) =>
    state.products.items.find((item) => item.id === id)
  )

  const [product, setProduct] = useState(storeProduct)
  const [isLoading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const didMount = useDidMount(true)

  useEffect(() => {
    ;(async () => {
      try {
        if (!product || product.id !== id) {
          setLoading(true)
          const doc = await firebase.getSingleProduct(id)

          if (doc.exists) {
            const data = { ...doc.data(), id: doc.ref.id }

            if (didMount) {
              // @ts-ignore
              setProduct(data)
              setLoading(false)
            }
          } else {
            setError('Product not found.')
          }
        }
      } catch (err) {
        if (didMount) {
          setLoading(false)
          setError(err?.message || 'Something went wrong.')
        }
      }
    })()
  }, [id])

  return { product, isLoading, error }
}

export default useProduct
