/* eslint-disable no-use-before-define */
/* eslint-disable operator-linebreak */
import { getProducts } from 'api'
import Button from 'old/components2/Button'
import { Flexbox, QueryLoader } from 'components'

import { CHECKOUT_STEP_1, SHOP } from 'old/constants/routes'
import { displayMoney, calculateTotal } from 'utils'
import { basketContext } from 'providers/BasketProvider'
import React, { useContext } from 'react'
import { useTranslation } from 'react-i18next'
import { useQuery } from 'react-query'
import { useHistory, useLocation } from 'react-router-dom'
import { basketToggleContext } from 'providers/BasketToggleProvider'
import { Product } from 'models/product'
import { BasketItem } from 'models/basketItem'

const Checkout = () => {
  const { t } = useTranslation('common')
  const { pathname } = useLocation()
  const history = useHistory()

  const { basket, basketLength } = useContext(basketContext)

  const productsQuery = useQuery('productsQuery', () => getProducts())

  const { closeBasket } = useContext(basketToggleContext)

  return (
    <QueryLoader query={productsQuery}>
      {(products) => (
        <Flexbox
          background="white"
          padding="m-size"
          alignItems="center"
          width="100%"
        >
          <div>
            <p className="basket-total-title">{t('basket.total')}</p>
            <h2 className="basket-total-amount">
              {displayMoney(
                calculateTotal(
                  basket.map((product) => getProductTotal(products, product))
                )
              )}
            </h2>
          </div>

          <Button
            variant="secondary"
            marginLeft="auto"
            onClick={() => {
              closeBasket()
              history.push(SHOP)
            }}
          >
            {t('basket.backToShop')}
          </Button>

          <Button
            marginLeft="10px"
            onClick={() => {
              closeBasket()
              history.push(CHECKOUT_STEP_1)
            }}
            disabled={!basketLength || pathname === '/checkout'}
          >
            {t('basket.checkout')}
          </Button>
        </Flexbox>
      )}
    </QueryLoader>
  )
}

const getProductTotal = (products: Product[], basketItem: BasketItem) => {
  const price = products.find((elem) => elem.id === basketItem.id)?.price || 0

  return price * basketItem.quantity
}

export default Checkout
