/* eslint-disable no-use-before-define */
import { TOptions, StringMap } from 'i18next'
import { useTranslation as useT, UseTranslationOptions } from 'react-i18next'
import { config, TranslationsNamespace } from 'i18n/config'

export default function useTranslation(
  ns: TranslationsNamespace | TranslationsNamespace[],
  options?: UseTranslationOptions
) {
  const { t, i18n, ready } = useT(ns, options)

  // Wrapper to silence TS errors complaining about t not always returning a string.
  const translate: TranslateFunc = (key, tOptions) =>
    t(key, tOptions).toString()

  const withBase =
    (base: string): TranslateFunc =>
    (key, tOptions) =>
      t(`${base}${config.keySeparator}${key}`, tOptions)

  return {
    t: translate,
    withBase,
    i18n,
    ready
  }
}

export type TranslateFunc = (
  key: string | string[],
  tOptions?: TOptions<StringMap> | string
) => string
