/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-use-before-define */
import { useTranslation } from 'react-i18next'
import { displayMoney } from 'old/helpers/utils'
import React from 'react'
import Select from 'react-select'
import { Formik, Form as FormikForm, Field, FieldProps } from 'formik'
import { Product } from 'models/product'
import useForm, { FormValues } from './useForm'

type Props = {
  product: Product
  onSelect: (value: string) => void
}

const Form = ({ product, onSelect }: Props) => {
  const { t } = useTranslation('viewProduct')

  const {
    schema,
    initialValues,
    handleAddProductToBasket,
    getIsInBasket,
    setVariant,
    submitForm,
    variant
  } = useForm(product)

  const isInBasket = getIsInBasket()

  return (
    <Formik
      initialValues={initialValues}
      validateOnChange
      validationSchema={schema}
      onSubmit={submitForm}
    >
      {() => (
        <FormikForm>
          {product.variants && (
            <Field name="email" type="email">
              {(fieldProps: FieldProps<'string', FormValues>) => (
                <div>
                  <span className="text-subtle">{product.variantsLabel}</span>
                  <br />
                  <br />

                  <Select
                    placeholder={t('selectPlaceholder')}
                    onChange={(option) => {
                      setVariant(option?.value)
                      onSelect(option?.value)
                    }}
                    options={product.variants
                      ?.sort((a, b) => (a < b ? -1 : 1))
                      .map((option) => ({
                        label: option.label,
                        value: option.value
                      }))}
                    styles={{
                      menu: (provided: any) => ({ ...provided, zIndex: 10 })
                    }}
                  />
                </div>
              )}
            </Field>
          )}

          <h1>{displayMoney(product.price)}</h1>

          <div className="product-modal-action">
            <button
              disabled={product.variants && !variant}
              className={`button button-small ${
                isInBasket ? 'button-border button-border-gray' : ''
              }`}
              onClick={handleAddProductToBasket}
              type="button"
            >
              {isInBasket ? t('removeFromBasket') : t('addToBasket')}
            </button>
          </div>
        </FormikForm>
      )}
    </Formik>
  )
}

export default Form
