import React, { ReactNode } from 'react'
import styled, { css } from 'styled-components'
import { SpaceProps, space } from 'styled-system'

type Props = {
  disabled?: boolean
  onClick?: () => void
  children: ReactNode
  variant?: Variant
  type?: 'button' | 'submit' | 'reset'
  size?: Size
} & SpaceProps

const Button = ({
  variant = 'primary',
  disabled,
  onClick,
  children,
  type = 'button',
  size = 'large',
  ...props
}: Props) => (
  <Container
    {...props}
    size={size}
    variant={variant}
    disabled={disabled}
    onClick={onClick}
    type={type}
  >
    {children}
  </Container>
)

type ContainerProps = SpaceProps & {
  variant: Variant
  size: Size
}

const Container = styled.button<ContainerProps>`
  background: ${(props) => props.theme.colors['button-color']};
  padding: ${(props) => props.theme.space['m-size']};
  border: 1px solid ${(props) => props.theme.colors['button-color']};
  color: ${(props) => props.theme.colors['white']};
  font-weight: bold;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  /* @include bezier-transition(all, 0.3s, ease); */

  &:hover {
    cursor: pointer;
    background: ${(props) => props.theme.colors['button-hover']};
    border: 1px solid ${(props) => props.theme.colors['button-hover']};
    text-decoration: none;
  }

  ${(props) =>
    props.variant === 'secondary' &&
    css`
      background: transparent;
      border: 1px solid ${(props) => props.theme.colors['button-color']};
      color: ${(props) => props.theme.colors['button-color']};

      &:hover {
        background: ${(props) => props.theme.colors['border-color']};
        border: 1px solid ${(props) => props.theme.colors['button-color']};
      }
    `}

  ${(props) =>
    props.variant === 'ternary' &&
    css`
      background: transparent;
      border: 1px solid ${(props) => props.theme.colors['border-color']};
      color: ${(props) => props.theme.colors['paragraph-color']};

      &:hover {
        border: 1px solid ${(props) => props.theme.colors['border-color']};
        background: ${(props) => props.theme.colors['border-color']};
      }
    `}


    ${(props) =>
    props.size === 'small' &&
    css`
      font-size: ${(props) => props.theme.fontSizes['font-small']};
      padding: ${(props) =>
        `${props.theme.space['s-size']} ${props.theme.space['m-size']}`};
    `}

  ${(props) =>
    props.disabled &&
    css`
      opacity: 0.5;

      &:hover {
        cursor: not-allowed;
      }
    `}

  ${space}
`

type Size = 'large' | 'small'
type Variant = 'primary' | 'secondary' | 'ternary'

export default Button
