import { Profile } from 'api/getProfile'
import { User } from 'firebase/auth'
import React, { createContext, ReactNode, useState } from 'react'
import { Role } from 'old/types'

type ProfileContextType = {
  profile?: Profile
  setProfile: React.Dispatch<React.SetStateAction<Profile | undefined>>
}

export const profileContext = createContext<ProfileContextType>({
  profile: undefined,
  setProfile: () => undefined
})

type Props = {
  children: ReactNode
}

const ProfileProvider = ({ children }: Props) => {
  const [profile, setProfile] =
    useState<ProfileContextType['profile']>(undefined)

  return (
    <profileContext.Provider value={{ profile, setProfile }}>
      {children}
    </profileContext.Provider>
  )
}

export default ProfileProvider
