/* eslint-disable implicit-arrow-linebreak */
import { Product } from 'api/getProducts'
import { basketContext } from 'providers/BasketProvider'
import { useContext, useState } from 'react'
import { string, object } from 'yup'

export type FormValues = {
  variant: string
}

const useForm = (product: Product) => {
  const [variant, setVariant] = useState<
    Product['variants'][number]['value'] | undefined
  >(undefined)
  const { basket, setBasket } = useContext(basketContext)

  const getIsInBasket = () =>
    basket.find((item) => {
      if (variant) {
        return product.id === item.id && variant === item.variant
      }
      return product.id === item.id
    })

  const isInBasket = getIsInBasket()

  const handleAddProductToBasket = () => {
    setBasket((prev) => {
      if (isInBasket) {
        const productInBasket = prev.find((elem) => {
          if (variant) {
            return product.id === elem.id && variant === elem.variant
          }
          return elem.id === product.id
        })

        return prev.filter((elem) => elem !== productInBasket)
      }

      const newBasketProduct = {
        id: product.id,
        quantity: 1,
        variant
      }
      return [...prev, newBasketProduct]
    })
  }

  const initialValues = product.variants
    ? {
        variant: undefined
      }
    : {}

  const schemaWhenVariants = object({
    variant: string()
  })

  const schemaWhenNoVariants = object({})

  const schema = product.variants ? schemaWhenVariants : schemaWhenNoVariants

  const submitForm = () => {}

  return {
    handleAddProductToBasket,
    getIsInBasket,
    setVariant,
    variant,
    initialValues,
    schema,
    submitForm
  }
}

export default useForm
